.courseware {
  background: #2a273b;
  width: 100%;
  height: calc(100% - 100px);
  padding: 19px;
}
.courseware .courseware-top {
  margin-left: 51px;
  display: flex;
  justify-content: space-between;
}
.courseware .courseware-top p {
  font-size: 16px;
  color: #fff;
  line-height: 16px;
  border-left: 2px solid #fff;
  padding-left: 8px;
  height: 18px;
}
.courseware .courseware-top .ant-btn {
  width: 160px;
  height: 44px;
  background: #302e47;
  border-radius: 30px !important;
  color: rgba(255, 255, 255, 0.8);
  border: none;
}
.courseware .courseware-content {
  width: 95%;
  background: #302e45;
  box-shadow: 0px 2px 10px 0px #232031;
  margin: 10px 0 0 38px;
  display: flex;
}
.courseware .courseware-content .ant-row {
  width: 300px;
  height: 100px;
  color: rgba(255, 255, 255, 0.7);
  padding: 20px 17px;
  margin: 15px 20px;
  font-size: 12px;
  transition: all 0.2s linear;
}
.courseware .courseware-content .ant-row:hover {
  background: #222736;
}
.courseware .courseware-content .ant-col img {
  width: 50px;
  height: 50px;
}
